import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List,  id   } from'@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Persik = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Предметы
		</PanelHeader>
		<Group title={<Header mode="secondary">Предметы</Header>}>
		 <List>
                <Cell onClick={go} data-to="DZ1">Ускорители</Cell>
                <Cell onClick={go} data-to="DZ2">Питомец</Cell>
                <Cell onClick={go} data-to="DZ3">Дом</Cell>
                <Cell onClick={go} data-to="DZ4">Рейтинг</Cell>
                <Cell onClick={go} data-to="DZ5">Бизнес</Cell>
                <Cell onClick={go} data-to="DZ6">Ферма</Cell>
                
                <Cell onClick={go} data-to="dze">Привелегии</Cell>
               


              </List>
		</Group>
	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;
