import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Dze = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="persik">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Привилегии
		</PanelHeader>
		<Group title={<Header mode="secondary">Привилегии</Header>}>
		 <List>
                <Cell>Игрок</Cell>
                <Cell>Вип</Cell>
                <Cell>Премиум</Cell>
                <Cell onClick={go} data-to="al1">Tester</Cell>
                <Cell>Мл.Модератор</Cell>
                <Cell>Модератор</Cell>
                <Cell>Ст.Модератор</Cell>
                <Cell>Мл.Администратор</Cell>
                <Cell>Администратор</Cell>
                <Cell>Ст.Администратор</Cell>
                <Cell>Владелец</Cell>

              </List>
		</Group>
	</Panel>
);

Dze.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Dze;