import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List, setState, Avatar, Footer, Switch, Tooltip,  Button, openTab  } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';




const osName = platform();

const Info = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Инфа
		</PanelHeader>
		<Group title={<Header mode="secondary">Информация о нас</Header>}>
		 <List>
		  <Cell before={<Avatar src= "https://sun9-42.userapi.com/c857636/v857636494/1722c6/lnjTt9adyF4.jpg" />}description="Игровой сервис">Daycoreclicker</Cell>

               
              </List>

              </Group>
               <Group title={<Header mode="secondary">Создатели</Header>}>

 <Cell
          size="l"
          description="Владелец"
          before={<Avatar src="https://sun9-41.userapi.com/c854016/v854016665/53c45/ahQLUB-emhY.jpg"/>}
          bottomContent={<Button component= "a" href="https://vk.com/vk_moders_dev">Добавить</Button>}
        >
          Роман Пух
        </Cell>




 <Cell
          size="l"
          description="Владелец"
          before={<Avatar src="https://sun1-99.userapi.com/c855736/v855736407/136733/nlorvqFegTM.jpg"/>}
          bottomContent={<Button component= "a" href="https://vk.com/irbis_ttr140">Добавить</Button>}
        >
          Алан Панов
        </Cell>










              </Group>


 <Group title={<Header mode="secondary">Настройки</Header>}>
              <List>
                <Cell onClick={go} data-to="profil">Профиль</Cell>
                <Cell onClick={go} data-to="DZ3">Основные</Cell>
                <Cell onClick={go} data-to="DZ3">Приватность</Cell>
                  <Cell asideContent={<Switch />}>
             Test
             </Cell>
                <Cell before={<Icon24Notification />}>Уведомления</Cell>
              </List>
               <Footer> Версия сервиса 1.1</Footer>







            </Group>





	</Panel>
);

Info.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Info;