import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Header, Group, Cell, List, Link, Separator  } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Profil = ({ go,id}) => (
	<Panel id={id}>
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="info">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Профиль
		</PanelHeader>
		
				

		<Group title={<Header mode="secondary">Ваши данные</Header>}>
		 <List>
       <Header aside={<Link>В разработке</Link>}>
        Имя:
      </Header>
      <Separator />
      <Header aside={<Link>Пользователь</Link>}>
        Статус:
      </Header>
      <Separator />


              </List>
		</Group>
	</Panel>
);

Profil.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Profil;